$text: rgb(55, 55, 55);
$textBright: rgb(213, 226, 221);
$background: rgb(213, 226, 221);
$primary: rgb(248, 126, 64);
$primaryDark: rgb(55, 55, 55);
$secondary: rgb(239, 106, 48);
$accent: rgb(78, 183, 180);
$bRadius: .75rem;

@media (max-width: 450px) {
  body {
    overflow-x: hidden;
  }

  header {
    flex: 1;
    font-family: 'Raleway', sans-serif;
    background: $primaryDark;
    display: flex;
    flex-direction: column;
    min-width: 100vw;
    justify-content: space-between;

    padding: 1vh 0;
    margin: 0;
    width: 100vw;
    max-width: 100vw;
    min-height: 10vh;

    a.navbar {
      flex: 1;
      margin: 0.5vh 0;
      color: $textBright;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 1.5em;
      text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
      text-decoration: none;
    }
  }

  a#imgHome {
    flex: 0.5;
  }
}

@media (min-width: 450px) {
  body {
    overflow-x: hidden;
  }

  header {
    font-family: 'Raleway', sans-serif;
    background: $primaryDark;
    display: flex;
    flex-direction: row;
    min-width: 100vw;
    justify-content: space-between;

    padding: 1vh 0;
    margin: 0;
    width: 100vw;
    max-width: 100vw;
    height: 10vh;


      input {
        height: 4vh;
        width: 15vw;
        border: transparent;
        border-radius: 5px;
        padding-left: 1vw;
        font-family: 'Raleway', sans-serif;
        font-size: 1em;
      }

      :focus-visible {
        outline: none;
        font-family: 'Raleway', sans-serif;
        font-size: 1em;
      }
    }

    a.navbar {
      flex: 1;
      color: $textBright;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 1.5em;
      text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
      text-decoration: none;
    }

    a:hover {
      text-decoration: $textBright underline;
    }
  }

  a#imgHome {
    flex: 1;
  }
