footer {
  position: sticky;
  color: rgb(213, 226, 221);
  padding: 2vh 0;
  margin: 0;
  display: flex;
  align-items: flex-end;
  text-align: right;
  justify-content: flex-end;
  flex-direction: row;
  min-height: 8vh;
  width: 100vw;
  max-width: 100vw;
  background: rgb(55, 55, 55);
}
footer a {
  color: rgb(213, 226, 221);
  text-decoration: none;
}
footer a:hover {
  text-decoration: underline;
}

